.container {
  pointer-events: auto;
  display: flex;
}

.link {
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  opacity: 0.9;
  transition: transform 0.2s ease-in-out;

  & + & {
    margin-left: 0.5em;
  }

  &:hover {
    opacity: 1;
    transform: scale(1.25);
  }

  svg {
    width: 100%;
    height: 100%;

    fill: var(--color-text);
  }
}
